import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio/ngx';
import { Platform, ToastController } from '@ionic/angular';
import { Router } from '@angular/router';


@Component({
  selector: 'app-fingerprint',
  templateUrl: './fingerprint.page.html',
  styleUrls: ['./fingerprint.page.scss'],
})
export class FingerprintPage implements OnInit {

  currentUser: any;

  constructor(private router: Router,
    private faio: FingerprintAIO,
    private plt: Platform,
    private toastController: ToastController,
    private dataService: DataService) { }

  ngOnInit() {
    this.currentUser = this.dataService.getCurrentUser();
  }

  ionViewWillEnter() {
    this.plt.ready().then(() => {
      this.showBioDialog();
    });
  }

  showBioDialog() {
    this.faio.isAvailable().then((result: any) => {
      console.log("result of bio", result);
      if (result === "finger" || result === "biometric") {
        this.verify();
      } else {
        this.presentToast("Biometric Authentication Not Available in your device!");
        this.router.navigate(["login-landing"], { replaceUrl: true });
      }
    }).catch((error: any) => {
      console.log(error);
      this.presentToast("Biometric Authentication Not Available in your device!");
      this.router.navigate(["dashboard"], { replaceUrl: true });
    });
  }

  verify() {
    this.faio.show({
      title: 'Demobank Biometric Verification', // (Android Only) | optional | Default: "<APP_NAME> Biometric Sign On"
      disableBackup:false,
    }).then((result: any) => {
      localStorage.setItem("isBioAuth", "true");
      this.router.navigate(["dashboard"], { replaceUrl: true });
      this.presentToast("Biometric Verified!");
    }).catch((error: any) => {
      console.log("Bio error ", error);
      if (error.code == -102) {
        this.verify();
      } else {
        this.presentToast(`${error.message}`);
        this.router.navigate(["login-landing"], { replaceUrl: true });
      }
    });
  }

  async presentToast(msg, position?) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 800,
      position: position || 'bottom'
    });
    toast.present();
  }



  back() {
    this.router.navigate(['login-landing']);
  }
}
